/* eslint-disable @typescript-eslint/no-unused-expressions */
/* eslint-disable prettier/prettier */
/* eslint-disable no-fallthrough */
/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect, useMemo, useRef, useReducer } from 'react';
import * as _ from 'lodash';
import * as fromReport from 'store/report/shared/selectors';
import * as fromAuth from 'store/auth/shared/selectors';
import * as CardSlice from 'store/card/shared/slice';
import { RESPONSE_MESSAGE } from 'store/report/constants/report.constant';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import { sliceKey, reducer, actions } from 'store/report/shared/slice';
import { ReportFormSaga } from 'store/report/shared/saga';
import { CardFormSaga } from 'store/card/shared/saga';
import {
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@material-ui/core';
import { AppHelper, Order } from 'utils/app.helper';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import { TekDatePicker } from 'app/components/InputDatePicker';
import {
  DEFAULT_FORMAT_DATE,
  DEFAULT_FORMAT_DATE_TIME,
  DEFAULT_FORMAT_INPUT_DATE,
  EMPTY_MESSAGE,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { CurrencyService } from 'services/currency.service';
import {
  LocalStorageService,
  LocalStorageKey,
} from 'services/localStorage.service';
import {
  makeStyles,
  withStyles,
  Theme,
  createStyles,
} from '@material-ui/core/styles';
import { NotifyService } from 'services/notify.service';
import { useSnackbar } from 'notistack';
import { RootStore } from 'store/configureStore';
import { Unsubscribe } from 'redux';
import TableHeaderSort from 'app/components/TableHeaderSort';
import TablePagination from '@material-ui/core/TablePagination';
import AppLoading from 'app/components/Loading';
import DateFnsUtils from '@date-io/date-fns';
import viLocale from 'date-fns/locale/vi';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { useReactToPrint } from 'react-to-print';
import { PdfTypeCard } from 'app/containers/ReportPage/components/PdfTypeCard';
import { PdfOverView } from '../components/PdfOverView';
import { PdfRegister } from '../components/PdfRegister';
import { PdfRecharged } from '../components/PdfRecharged';
import { PdfReturn } from '../components/PdfReturn';
import { PdfCardFee } from '../components/PdfCardFee';

export const useStyles = makeStyles({
  root: {
    width: '100%',
  },
  container: {
    height: '100%',
  },
  table: {
    minWidth: '100vw',
    overflowX: 'scroll',
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
});

const StyledTableCell = withStyles((theme: Theme) =>
  createStyles({
    head: {
      backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    body: {
      fontSize: 14,
    },
  }),
)(TableCell);

const StyledTableRow = withStyles((theme: Theme) =>
  createStyles({
    root: {
      '&:nth-of-type(odd)': {
        backgroundColor: theme.palette.action.hover,
      },
    },
  }),
)(TableRow);

export const columnListReport: any[] = [
  { id: 'row_num', label: 'S phiu', minWidth: 150 },
  {
    id: 'tekmedi_card_number',
    label: 'M th BN',
    minWidth: 150,
  },
  { id: 'patient_code', label: 'M bnh nhn', minWidth: 150 },
  { id: 'patient_name', label: 'Tn bnh nhn', minWidth: 150 },
  {
    id: 'time',
    label: 'Ngy gi',
    minWidth: 150,
  },
  {
    id: 'payment_type_name',
    label: 'Loi hnh',
    minWidth: 150,
  },
  {
    id: 'price',
    label: 'S tin',
    minWidth: 150,
  },
  {
    id: 'employee_name',
    label: 'Nhn vin',
    minWidth: 150,
  },
  {
    id: 'type',
    label: 'Bo co',
    minWidth: 150,
  },
];

const report = [
  { code: '0', label: 'Tng qut' },
  { code: '1', label: 'Ghi th' },
  { code: '2', label: 'Np th' },
  { code: '3', label: 'Tr th' },
  { code: '11', label: 'Mt th' },
];

enum TypeCard {
  WRITE_CARD = '1',
  UP_CARD = '2',
  RETURN_CARD = '3',
  LOST_CARD = '11',
}

const defaultPagi = {
  page: 1,
  rowsPerPage: 10,
};

export function ReportCard() {
  const classes = useStyles();
  const local = new LocalStorageService();
  useInjectReducer({ key: sliceKey, reducer: reducer });
  useInjectSaga({ key: sliceKey, saga: ReportFormSaga });
  useInjectReducer({ key: CardSlice.sliceKey, reducer: CardSlice.reducer });
  useInjectSaga({ key: CardSlice.sliceKey, saga: CardFormSaga });
  const listCard = useSelector(fromReport.selectDataListHistoryTekCard);
  const filePdf = useSelector(fromReport.selectFilePdf);
  const filePdfOver = useSelector(fromReport.selectFilePdfOver);
  const filePdfCardFee = useSelector(fromReport.selectFilePdfCardFee);
  const filePdfRegister = useSelector(fromReport.selectFilePdfRegister);
  const filePdfRecharged = useSelector(fromReport.selectFilePdfRecharged);
  const filePdfReturn = useSelector(fromReport.selectFilePdfReturn);
  const user: any = useSelector<any>(fromAuth.selectUser);
  const loading = useSelector(fromReport.selectLoading);
  const [isOpenDisplay, setIsOpenDisplay] = useState<boolean>(false);
  const [isCloseTable, setIsCloseTable] = useState<boolean>(false);
  const dispatch = useDispatch();
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('name');
  const dateUtil = new DateFnsUtils();
  const { enqueueSnackbar } = useSnackbar();
  const notifyController = new NotifyService(enqueueSnackbar);
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(new Date());
  const [errDate, setErrDate] = useState<any>(false);
  const [pagi, setPagi] = useState<any>(defaultPagi);
  const [statusRp, setStatusRp] = useState<any>(report[0].code);

  let printRef: any = useRef<HTMLDivElement>(null);
  const reactToPrintContent = React.useCallback(() => {
    return printRef.current;
  }, [printRef.current]);
  const handlePrint = useReactToPrint!({
    content: reactToPrintContent,
    removeAfterPrint: true,
  });

  let printRefOv: any = useRef<HTMLDivElement>(null);
  const reactToPrintContentOv = React.useCallback(() => {
    return printRefOv.current;
  }, [printRefOv.current]);
  const handlePrintOv = useReactToPrint!({
    content: reactToPrintContentOv,
    removeAfterPrint: false,
  });

  useEffect(() => {
    dispatch(actions.getListHistoryTekmediCard({ ...getFromValues() }));
    const storeSub$: Unsubscribe = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case actions.getListHistoryTekmediCardSuccess.type:
          notifyController
            .setNotifyMessage(
              RESPONSE_MESSAGE.HISTORY_CARD.GET_LIST_HISTORY_SUCCESS,
            )
            .setSuccessNotify();
          break;
        case actions.exportHistoryCardSuccess.type:
          notifyController
            .setNotifyMessage(
              RESPONSE_MESSAGE.HISTORY_CARD.EXPORT_HISTORY_CARD_SUCCESS,
            )
            .setSuccessNotify();
          AppHelper.createDownloadFile({
            file_path: payload.file_path,
            file_name: payload.file_name,
          });
          break;
        case actions.exportHistoryCardPdfOverSuccess.type:
          break;
        case actions.getListHistoryTekmediCardFail.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : RESPONSE_MESSAGE.HISTORY_CARD.GET_LIST_HISTORY_FAIL,
            )
            .setFailNotify();
          break;
        case actions.exportHistoryCardFail.type:
        case actions.exportHistoryCardPdfFail.type:
          notifyController
            .setNotifyMessage(
              payload
                ? payload
                : RESPONSE_MESSAGE.HISTORY_CARD.EXPORT_HISTORY_CARD_FAIL,
            )
            .setFailNotify();
          break;
        default:
          break;
      }
    });
    dispatch(CardSlice.actions.getListValueExtend());
    return () => {
      storeSub$();
      dispatch(actions.clearFilePdf());
      dispatch(actions.clearFilePdfOver());
    };
  }, []);

  useEffect(() => {
    if (!_.isEmpty(filePdf)) {
      handlePrint!();
    }
  }, [filePdf]);

  useEffect(() => {
    if (!_.isEmpty(filePdfOver)) {
      handlePrintOv!();
    }
  }, [filePdfOver]);

  // useEffect(() => {
  //   const string = renderToString(<PrintPdfOverView info={filePdfOver} />);
  //   const pdf: any = new jsPDF();
  //   if (!_.isEmpty(filePdfOver)) {
  //     setTimeout(() => {
  //       pdf.html(string, {
  //         callback: function (doc: any) {
  //           doc.setFontSize(3);
  //           doc.addFont('roboto', 'normal')
  //           doc.save();
  //         },
  //       });
  //     });
  //   }
  // }, [filePdfOver]);

  const renderData: any = useMemo(() => {
    const { data = [], total_records } = listCard;
    return { data, total_records };
  }, [listCard]);

  const getFromValues = () => {
    return {
      admin_View: true,
      type_Card: statusRp,
      employee_Id: user.id,
      employee_Name: user.full_name,
      start_Date: dateUtil.format(startDate, DEFAULT_FORMAT_INPUT_DATE),
      end_Date: dateUtil.format(endDate, DEFAULT_FORMAT_INPUT_DATE),
      page_Number: pagi.page === 0 ? 1 : pagi.page,
      page_Size: pagi.rowsPerPage,
    };
  };

  const handleRequestSort = (e, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleChangeDateStart = date => {
    if (date > endDate) {
      setErrDate(true);
      setStartDate(new Date(date));
    } else {
      setErrDate(false);
      setStartDate(new Date(date));
    }
  };

  const handleChangeDateEnd = date => {
    if (date < startDate) {
      setErrDate(true);
      setEndDate(new Date(date));
    } else {
      setErrDate(false);
      setEndDate(new Date(date));
    }
  };

  const submitSearch = e => {
    e.preventDefault();
    dispatch(actions.getListHistoryTekmediCard({ ...getFromValues() }));
  };

  const exportFileHistoryCards = () => {
    dispatch(actions.exportHistoryCard({ ...getFromValues() }));
  };
  const exportFileHistoryCardPdf = () => {
    if (statusRp !== '0') {
      dispatch(actions.exportHistoryCardPdf({ ...getFromValues() }));
    }
    if (statusRp === '0') {
      dispatch(actions.exportHistoryCardPdfOver({ ...getFromValues() }));
    }
  };

  const handleIsOpenDisplay = () => {
    setIsOpenDisplay(!isOpenDisplay);
  };
  const handleCloseTable = () => {
    setIsCloseTable(!isCloseTable);
  };
  const handlerefresh = () => {
    setStartDate(new Date());
    setEndDate(new Date());
    setStatusRp(report[0].code);
    setErrDate(false);
    setPagi(defaultPagi);
    dispatch(
      actions.getListHistoryTekmediCard({
        ...getFromValues(),
        start_Date: new Date(),
        end_Date: new Date(),
        page_Number: 1,
        page_Size: 10,
        type_Card: report[0].code,
      }),
    );
  };

  const renderNavControl = () => {
    return (
      <form onSubmit={submitSearch} className="row">
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
          <div className="col-xl-2 col-lg-2 form-group">
            <label className="font-weight-bold">Thi gian bt u</label>
            <DatePicker
              value={startDate}
              onChange={handleChangeDateStart}
              format={DEFAULT_FORMAT_DATE}
              TextFieldComponent={TekDatePicker}
              maxDate={new Date()}
            />
            {errDate && (
              <span style={{ color: ' #fb0606' }}>
                Ngy bt u phi nh hn ngy kt thc
              </span>
            )}
          </div>
          <span className="span__line">-</span>
          <div className="col-xl-2 col-lg-2 form-group">
            <label className="font-weight-bold">Thi gian kt thc</label>
            <DatePicker
              value={endDate}
              onChange={handleChangeDateEnd}
              format={DEFAULT_FORMAT_DATE}
              TextFieldComponent={TekDatePicker}
              maxDate={new Date()}
            />
          </div>
        </MuiPickersUtilsProvider>
        <div className="col-xl-2 col-lg-2 form-group">
          <label className="font-weight-bold">Bo co</label>
          <FormControl fullWidth>
            <Select
              id="report"
              name="report"
              className="style_select"
              value={statusRp}
              variant="outlined"
              onChange={e => setStatusRp(e.target.value)}
            >
              {report.map(({ code, label }) => (
                <MenuItem value={code} key={code}>
                  {label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </div>
        <div className="col-xl-1 col-lg-1 btn-group mg-t">
          <button
            className="btn btn-primary height39"
            type="submit"
            disabled={errDate}
          >
            Tm kim <i className="fa fa-search"></i>
          </button>
        </div>
        <div className="col-xl-4 col-lg-12 btn-group mg-t">
          <button
            disabled={errDate}
            className="btn btn-success height39 mr-2"
            type="button"
            onClick={exportFileHistoryCards}
          >
            Xut file Excel<i className="fa fa-upload"></i>
          </button>
          <button
            className="btn btn-danger height39"
            type="button"
            disabled={errDate}
            onClick={exportFileHistoryCardPdf}
          >
            Xut File PDF<i className="fa fa-upload"></i>
          </button>
        </div>
      </form>
    );
  };

  const renderColumns = () => (
    <TableHeaderSort
      classes={classes}
      onRequestSort={handleRequestSort}
      order={order}
      orderBy={orderBy}
      columns={columnListReport}
    />
  );

  const renderTable = () => {
    if (!_.isEmpty(listCard) && listCard.data.length) {
      const tableData: any[] = AppHelper.stableSort(
        renderData.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, index) => {
        return (
          <StyledTableRow key={index} hover>
            {columnListReport.map(column => {
              const value = row[column.id];
              if (column.id === 'time') {
                return (
                  <StyledTableCell key={column.id}>
                    {value
                      ? dateUtil.format(
                          new Date(value),
                          DEFAULT_FORMAT_DATE_TIME,
                        )
                      : ''}
                  </StyledTableCell>
                );
              }
              if (column.id === 'price') {
                return (
                  <StyledTableCell key={column.id}>
                    {CurrencyService.formatCurrency(value)}
                  </StyledTableCell>
                );
              }
              if (column.id === 'type') {
                return (
                  <StyledTableCell key={column.id}>
                    {value === 1
                      ? 'Ghi th'
                      : value === 2
                      ? 'Np th'
                      : value === 3
                      ? 'Tr th'
                      : value === 11
                      ? 'Mt th'
                      : '-'}
                  </StyledTableCell>
                );
              }
              if (column.id === 'payment_type_name') {
                return (
                  <StyledTableCell key={column.id}>
                    {row.type === 2 && _.isEmpty(value)
                      ? 'Tin mt'
                      : row.type === 2 && !_.isEmpty(value)
                      ? value
                      : '-'}
                  </StyledTableCell>
                );
              }
              return (
                <StyledTableCell key={column.id}>
                  {value ? value : '-'}
                </StyledTableCell>
              );
            })}
          </StyledTableRow>
        );
      });
    } else {
      return (
        <StyledTableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={columnListReport.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </StyledTableRow>
      );
    }
  };

  const handleChangePage = (e: unknown, page: number) => {
    let changePage = Object.assign({}, pagi, {
      page: page + 1,
      rowsPerPage: pagi.rowsPerPage,
    });
    setPagi(changePage);
    dispatch(
      actions.getListHistoryTekmediCard({
        ...getFromValues(),
        page_Number: changePage.page,
      }),
    );
  };
  const handleChangeRowPerPage = e => {
    let changeRowPage = Object.assign({}, pagi, {
      rowsPerPage: +e.target.value,
      page: 1,
    });
    setPagi(changeRowPage);
    dispatch(
      actions.getListHistoryTekmediCard({
        ...getFromValues(),
        page_Size: changeRowPage.rowsPerPage,
        page_Number: 1,
      }),
    );
  };

  const renderPagination = () => {
    if (!_.isEmpty(renderData) && renderData.data.length) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={renderData.total_records}
          rowsPerPage={pagi.rowsPerPage}
          page={pagi.page - 1}
          labelRowsPerPage="Hin Th"
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowPerPage}
        />
      );
    }
  };

  return (
    <div
      id="test"
      className="row"
      style={{ display: isCloseTable ? 'none' : 'block' }}
    >
      <div className="d-none">
        <PdfTypeCard
          ref={printRef}
          info={filePdf}
          startDate={startDate}
          endDate={endDate}
          adminView={true}
          typeCard={statusRp}
        />
        <div ref={printRefOv}>
          <PdfOverView
            info={filePdfOver}
            startDate={startDate}
            endDate={endDate}
            adminView={true}
          />
          <PdfRegister
            info={filePdfRegister}
            startDate={startDate}
            endDate={endDate}
            adminView={true}
            typeCard={TypeCard.WRITE_CARD}
          />
          <PdfRecharged
            info={filePdfRecharged}
            startDate={startDate}
            endDate={endDate}
            adminView={true}
            typeCard={TypeCard.UP_CARD}
          />
          <PdfReturn
            info={filePdfReturn}
            startDate={startDate}
            endDate={endDate}
            adminView={true}
            typeCard={TypeCard.RETURN_CARD}
          />
          <PdfCardFee
            info={filePdfCardFee}
            startDate={startDate}
            endDate={endDate}
            adminView={true}
            typeCard={TypeCard.LOST_CARD}
          />
        </div>
      </div>
      <div className="col-md-12">
        <div className="card card-topline-red">
          <div className="card-head">
            <header></header>
            <div className="tools">
              <a
                className="fa fa-repeat btn-color box-refresh"
                onClick={handlerefresh}
              ></a>
              <a
                className={
                  isOpenDisplay
                    ? 't-collapse btn-color fa fa-chevron-down'
                    : 't-collapse btn-color fa fa-chevron-up'
                }
                onClick={handleIsOpenDisplay}
              ></a>
              <a
                className="t-close btn-color fa fa-times"
                onClick={handleCloseTable}
              ></a>
            </div>
          </div>
          <div
            className="card-body"
            style={{ display: isOpenDisplay ? 'none' : 'block' }}
          >
            {renderNavControl()}
            <Paper className={classes.root}>
              {loading ? <AppLoading loading={true} /> : null}
              <TableContainer className={classes.container}>
                <Table stickyHeader>
                  <TableHead>{renderColumns()}</TableHead>
                  <TableBody>{renderTable()}</TableBody>
                </Table>
              </TableContainer>
              {renderPagination()}
            </Paper>
          </div>
        </div>
      </div>
    </div>
  );
}
