/* eslint-disable jsx-a11y/anchor-is-valid */

import { PayloadAction } from '@reduxjs/toolkit';
import { createSlice } from 'store/core/@reduxjs/toolkit';

export interface TransactionVCBState {
  dataListTranSaction: any;
  dataTransaction: any;
  dataTransactionDetail: any;
  dataTransactionDetailModal: any;
  success: any;
  loading: boolean;
  error: any;
}
export const initialState: TransactionVCBState = {
  dataListTranSaction: {},
  dataTransaction: {},
  dataTransactionDetail: {},
  dataTransactionDetailModal: {},
  loading: false,
  error: null,
  success: {},
};

const transactionVCBSlice = createSlice({
  name: 'transactionVCB',
  initialState,
  reducers: {
    getListTranSaction(state, action: any) {
      state.loading = true;
      state.error = null;
    },
    getListTranSactionSuccess(state, action: any) {
      state.loading = false;
      state.dataListTranSaction = action.payload;
    },
    getListTranSactionFail(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },
    getListTransactionDetail(state, action: any) {
      state.loading = true;
      state.error = null;
    },
    getListTransactionDetaiSuccess(state, action: any) {
      state.loading = false;
      state.dataTransactionDetail = action.payload;
    },
    getListTransactionDetaiFail(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },
    getListTransactionDetailModal(state, action: any) {
      state.loading = true;
      state.error = null;
    },
    getListTransactionDetailModalSuccess(state, action: any) {
      state.loading = false;
      state.dataTransactionDetailModal = action.payload.result;
    },
    getListTransactionDetailModalFail(state, action: any) {
      state.loading = false;
      state.error = action.payload;
    },

    refund(state, action) {
      state.loading = true;
    },
    refundSuccess(state, action) {
      state.loading = false;
    },
    refundFailure(state, action) {
      state.loading = false;
    },

    clearSuccess(state) {
      state.success = {};
    },
    clearDetail(state) {
      state.dataTransactionDetailModal = null;
    },
    clear(state) {
      return initialState;
    },
  },
});

export const { actions, reducer, name: sliceKey } = transactionVCBSlice;
