/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable prettier/prettier */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/anchor-has-content */
/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { useState, useEffect, useMemo, useRef } from 'react';
import {
  useInjectReducer,
  useInjectSaga,
} from 'store/core/@reduxjs/redux-injectors';
import { useSelector, useDispatch } from 'react-redux';
import moment from 'moment';
import * as _ from 'lodash';
import { sliceKey, reducer, actions } from 'store/transactionVCB/shared/slice';
import { Link } from 'react-router-dom';
import {
  selectLoading,
  selectError,
  selectDataListTransaction,
} from 'store/transactionVCB/shared/selectors';
import {
  Tooltip,
  Paper,
  Table,
  TableCell,
  TableContainer,
  TablePagination,
  TableRow,
  Theme,
} from '@material-ui/core';
import {
  useStyles,
  useStylesDatepicker,
} from 'store/common/constants/common.constant';
import { AppHelper, Order } from 'utils/app.helper';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DateFnsUtils from '@date-io/date-fns';
import {
  DEFAULT_FORMAT_DATE,
  EMPTY_MESSAGE,
  DEFAULT_FORMAT_DATE_TIME,
  rowsPerArray,
} from 'store/common/constants/common.constant';
import { useSnackbar } from 'notistack';
import { CurrencyService } from 'services/currency.service';
import { useForm } from 'react-hook-form';
import { TransactionVCBSaga } from 'store/transactionVCB/shared/saga';
import { TekDatePicker } from 'app/components/InputDatePicker';
import STableHeaderSort from 'app/components/STableHeaderSort';
import AppLoading from 'app/components/Loading';
import viLocale from 'date-fns/locale/vi';
import 'react-datepicker/dist/react-datepicker.css';
import STableHead from 'app/components/STableHead';
import STableBody from 'app/components/STableBody';
import { selectUser } from 'store/auth/shared/selectors';
import { RootStore } from 'store/configureStore';
import { NotifyService } from 'services/notify.service';
import { RESPONSE_MESSAGE } from 'store/transactionVCB/constants/http.constant';
import { TransactionStatus } from 'store/transactionVCB/constants/http.constant';
import { ConfirmModal } from 'app/components/ConfirmModal';

interface FormIputs {
  status: string;
  patientCode: string;
  patientName: string;
}

export const transactionCols = [
  { id: 'action', label: 'Chc nng', align: 'center', minWidth: 95 },
  {
    id: 'tekmedi_card_number',
    label: 'M th',
    align: 'center',
    minWidth: 120,
  },
  {
    id: 'patient_code',
    label: 'M bnh nhn',
    align: 'center',
    minWidth: 160,
  },
  {
    id: 'registered_code',
    label: 'S tip nhn',
    align: 'center',
    minWidth: 130,
  },
  {
    id: 'full_name',
    label: 'H v tn',
    align: 'center',
    minWidth: 180,
  },
  { id: 'gender', label: 'Gii tnh', align: 'center', minWidth: 80 },
  { id: 'birthday', label: 'Nm sinh', align: 'center', minWidth: 70 },
  {
    id: 'created_date',
    label: 'Ngy to',
    align: 'center',
    minWidth: 100,
  },
  {
    id: 'updated_date',
    label: 'Ngy cp nht',
    align: 'center',
    minWidth: 100,
  },
  {
    id: 'amount',
    label: 'Tng tin hon tr',
    align: 'center',
    minWidth: 120,
  },
  { id: 'status', label: 'Trng thi', align: 'center', minWidth: 100 },
  {
    id: 'employee_name',
    label: 'Nhn vin',
    align: 'center',
    minWidth: 120,
  },
];

const initDate = moment().add(-1, 'day').toDate();

export function TransactionVietcombank() {
  const classes = useStyles();
  const datePickerClasses = useStylesDatepicker();
  useInjectReducer({ key: sliceKey, reducer: reducer });
  useInjectSaga({ key: sliceKey, saga: TransactionVCBSaga });
  const { enqueueSnackbar } = useSnackbar();
  const notifyControllerRef = useRef(new NotifyService(enqueueSnackbar));
  const [isOpenDisplay, setIsOpenDisplay] = useState<boolean>(false);
  const [isCloseTable, setIsCloseTable] = useState<boolean>(false);
  const user = useSelector(selectUser);
  const dataListTransaction: any = useSelector(selectDataListTransaction);
  const loading: any = useSelector(selectLoading);
  const error: any = useSelector(selectError);
  const dispatch = useDispatch();
  const dateUtil = new DateFnsUtils();
  const [order, setOrder] = useState<Order>('asc');
  const [orderBy, setOrderBy] = useState<any>('tekmedi_card_number');
  const [endDate, setDateEnd] = useState(initDate);
  const [startDate, setDateStart] = useState(initDate);
  const { register, handleSubmit, getValues, errors } = useForm<FormIputs>();
  const [filterTransation, setFilterTransation] = useState<any>({
    startDate: moment.utc(startDate).startOf('day'),
    endDate: moment.utc(endDate).endOf('day'),
    page: 0,
    rowsPerPage: 10,
    patientCode: '',
    patientName: '',
    status: '0',
  });
  const memoTransaction: any = useMemo(() => {
    const { data = [], recordsTotal } = dataListTransaction;
    return {
      data: data.map(d => ({
        ...d,
        full_name: `${d.last_name} ${d.first_name}`,
      })),
      recordsTotal,
    };
  }, [dataListTransaction]);

  const validateTransaction = () =>
    dataListTransaction.data && Object.entries(dataListTransaction.data).length
      ? true
      : false;
  const [submitedSearch, setSubmitedSearch] = useState<boolean>(false);
  useEffect(() => {
    if (submitedSearch) dispatch(actions.getListTranSaction(filterTransation));
  }, [submitedSearch, filterTransation]);

  const handleRequestSort = (e, property: any) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };
  const setNewFilter = newFilter => {
    setFilterTransation(newFilter);
  };
  const handleChangePage = (e: unknown, page: number) => {
    let changePage = Object.assign({}, filterTransation, { page });
    setNewFilter(changePage);
  };
  const handleChangeRowsPerpage = e => {
    let changeRowPage = Object.assign({}, filterTransation, {
      rowsPerPage: +e.target.value,
      page: 0,
    });
    setNewFilter(changeRowPage);
  };
  const handleOpenDisplay = () => {
    setIsOpenDisplay(!isOpenDisplay);
  };
  const handleCloseTable = () => {
    setIsCloseTable(!isCloseTable);
  };
  const handleRefresh = () => {
    setFilterTransation({
      startDate: moment.utc(startDate).startOf('day'),
      endDate: moment.utc(endDate).endOf('day'),
      page: 0,
      rowsPerPage: 10,
      patientCode: '',
      patientName: '',
      status: '0',
    });
  };
  const handleChangeStartDate = date => {
    setDateStart(new Date(date));
  };
  const handleChangeEndDate = date => {
    setDateEnd(new Date(date));
  };
  const onSubmit = e => {
    setFilterTransation({
      ..._.cloneDeep(filterTransation),
      startDate: moment.utc(startDate).startOf('day'),
      endDate: moment.utc(endDate).endOf('day'),
      status: getValues('status'),
      patientCode: getValues('patientCode'),
      patientName: getValues('patientName'),
      page: 0,
      rowsPerPage: 10,
    });
    setSubmitedSearch(true);
  };
  const renderNavBar = () => {
    return (
      <form onSubmit={handleSubmit(onSubmit)} className="row">
        <MuiPickersUtilsProvider utils={DateFnsUtils} locale={viLocale}>
          <div className="col-md-2 form-group">
            <label className="font-weight-bold">Thi gian</label>
            <DatePicker
              value={startDate}
              onChange={handleChangeStartDate}
              format={DEFAULT_FORMAT_DATE}
              TextFieldComponent={TekDatePicker}
              //maxDate={initDate}
            />
          </div>
          <span className="span__line">-</span>
          <div className="col-md-2 form-group mg-t">
            <DatePicker
              value={endDate}
              onChange={handleChangeEndDate}
              format={DEFAULT_FORMAT_DATE}
              TextFieldComponent={TekDatePicker}
              //maxDate={initDate}
            />
          </div>
        </MuiPickersUtilsProvider>
        <div className="col-md-2 form-group">
          <label className="font-weight-bold">M bnh nhn</label>
          <input
            type="text"
            className="form-control"
            name="patientCode"
            placeholder="M bnh nhn"
            ref={register({
              required: false,
              maxLength: 50,
              pattern: {
                value: AppHelper.removeSpaceStartEnd(),
                message: 'Vui lng xa khong trng',
              },
            })}
          />
          {errors.patientCode?.message && (
            <span style={{ color: '#fb0606' }}>
              {errors.patientCode?.message}
            </span>
          )}
        </div>
        <div className="col-md-2 form-group">
          <label className="font-weight-bold">Tn bnh nhn</label>
          <input
            type="text"
            className="form-control"
            name="patientName"
            placeholder="Tn bnh nhn"
            ref={register({
              required: false,
              maxLength: 50,
              pattern: {
                value: AppHelper.removeSpaceStartEnd(),
                message: 'Vui lng xa khong trng',
              },
            })}
          />
          {errors.patientName?.message && (
            <span style={{ color: '#fb0606' }}>
              {errors.patientName?.message}
            </span>
          )}
        </div>
        <div className="col-md-2 form-select">
          <label className="font-weight-bold">Trng thi</label>
          <select
            name="status"
            className="form-control"
            ref={register({ required: false })}
          >
            <option value={-1}>Tt c</option>
            {TransactionStatus.map(s => (
              <option key={s.value} value={s.value} style={{ color: s.color }}>
                {s.title}
              </option>
            ))}
          </select>
        </div>
        <div className="col-md-1 btn-group mg-t">
          <button className="btn btn-primary btn__config" type="submit">
            TM KIM <i className="fa fa-search"></i>
          </button>
        </div>
      </form>
    );
  };

  const renderColumns = () => (
    <STableHeaderSort
      classes={classes}
      onRequestSort={handleRequestSort}
      order={order}
      orderBy={orderBy}
      columns={transactionCols}
    />
  );
  const renderTable = () => {
    if (validateTransaction()) {
      const tableData: any[] = AppHelper.stableSort(
        memoTransaction.data,
        AppHelper.getComparator(order, orderBy),
      );
      return tableData.map((row, index) => {
        return (
          <TableRow key={index} hover>
            {transactionCols.map((column, idx) => {
              const value = row[column.id];
              if (column.id === '#') {
                return (
                  <TableCell key={idx} className={classes.td}>
                    {index + 1}
                  </TableCell>
                );
              } else if (
                column.id === 'created_date' ||
                column.id === 'updated_date'
              ) {
                return (
                  <TableCell key={idx} className={classes.td}>
                    {value
                      ? dateUtil.format(
                          new Date(value),
                          DEFAULT_FORMAT_DATE_TIME,
                        )
                      : '-'}
                  </TableCell>
                );
              } else if (column.id === 'birthday') {
                return (
                  <TableCell key={idx} className={classes.td}>
                    {value ? new Date(value).getFullYear() : '-'}
                  </TableCell>
                );
              } else if (column.id === 'amount') {
                return (
                  <TableCell key={idx} className={classes.td} align="right">
                    {CurrencyService.formatCurrency(value)}
                  </TableCell>
                );
              } else if (column.id === 'status') {
                const status = TransactionStatus.find(s => s.value === value);
                return (
                  <TableCell key={idx} className={classes.td}>
                    <span style={{ color: status?.color || 'black' }}>
                      {status?.title || value}
                    </span>
                  </TableCell>
                );
              } else if (column.id === 'action') {
                return (
                  <TableCell key={idx} className={classes.td}>
                    {!_.isEmpty(dataListTransaction) ? (
                      <>
                        {/* <Tooltip
                          title={'Xem chi tit'}
                          enterDelay={500}
                          leaveDelay={100}
                        >
                          <Link
                            className="btn btn-info btn-xs"
                            to={{
                              pathname: '/transactionVCB/detail',
                              search: `?patientCode=${patient_code}&registeredCode=${registered_code}&registeredDate=${registered_date}`,
                            }}
                          >
                            <i className="fa fa-eye"></i>
                          </Link>
                        </Tooltip> */}
                        {row?.status === 2 && (
                          <Tooltip
                            title={'Hon tr'}
                            enterDelay={500}
                            leaveDelay={100}
                          >
                            <div
                              className="btn btn-danger btn-xs"
                              onClick={() => {
                                setConfirmData(row);
                                setConfirmOpen(true);
                              }}
                            >
                              <i className="fa fa-money"></i>
                            </div>
                          </Tooltip>
                        )}
                      </>
                    ) : (
                      ''
                    )}
                  </TableCell>
                );
              } else {
                return (
                  <TableCell key={idx} className={classes.td}>
                    {value ? value : '-'}
                  </TableCell>
                );
              }
            })}
          </TableRow>
        );
      });
    } else {
      return (
        <TableRow>
          <TableCell
            className="w-100"
            align="center"
            colSpan={transactionCols.length}
          >
            {EMPTY_MESSAGE}
          </TableCell>
        </TableRow>
      );
    }
  };
  const renderPagination = () => {
    if (validateTransaction()) {
      return (
        <TablePagination
          rowsPerPageOptions={rowsPerArray}
          component="div"
          count={memoTransaction.recordsTotal}
          rowsPerPage={filterTransation ? filterTransation.rowsPerPage : 10}
          page={filterTransation ? filterTransation.page : 0}
          labelRowsPerPage="Hin Th"
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerpage}
        />
      );
    }
  };

  useEffect(() => {
    const unsub = RootStore.subscribe(() => {
      const { type, payload } = RootStore.getState().lastAction;
      switch (type) {
        case actions.refundSuccess.type:
          setConfirmData(null);
          setConfirmOpen(false);
          notifyControllerRef.current
            .setNotifyMessage(RESPONSE_MESSAGE.REFUND_SUCCESS)
            .setSuccessNotify();
          if (submitedSearch)
            dispatch(actions.getListTranSaction(filterTransation));
          break;
        case actions.refundFailure.type:
          setConfirmData(null);
          setConfirmOpen(false);
          notifyControllerRef.current.setNotifyMessage(payload).setFailNotify();
          break;
      }
    });
    return () => {
      unsub();
      dispatch(actions.clear());
    };
  }, [submitedSearch, filterTransation]);

  // Confirm
  const [confirmOpen, setConfirmOpen] = useState<boolean>(false);
  const [confirmData, setConfirmData] = useState<any>(null);

  return (
    <div>
      <div className="row" style={{ display: isCloseTable ? 'none' : 'block' }}>
        <div className="col-md-12">
          <div className="card card-topline-red">
            <div className="card-head">
              <header></header>
              <div className="tools">
                <a
                  className="fa fa-repeat btn-color box-refresh"
                  onClick={handleRefresh}
                ></a>
                <a
                  className={
                    isOpenDisplay
                      ? 't-collapse btn-color fa fa-chevron-down'
                      : 't-collapse btn-color fa fa-chevron-up'
                  }
                  onClick={handleOpenDisplay}
                ></a>
                <a
                  className="t-close btn-color fa fa-times"
                  onClick={handleCloseTable}
                ></a>
              </div>
            </div>
            <div
              className="card-body"
              style={{ display: isOpenDisplay ? 'none' : 'block' }}
            >
              {renderNavBar()}
              <Paper className={classes.root}>
                {loading ? <AppLoading loading={true} /> : null}
                <TableContainer className={classes.container}>
                  <Table stickyHeader aria-label="sticky table">
                    <STableHead>{renderColumns()}</STableHead>
                    <STableBody>{renderTable()}</STableBody>
                  </Table>
                </TableContainer>
                {renderPagination()}
              </Paper>
            </div>
          </div>
        </div>
      </div>

      <ConfirmModal
        open={confirmOpen}
        content="Bn chc chn mun hon tr?"
        onClose={() => {
          setConfirmOpen(false);
          setConfirmData(null);
        }}
        onCancel={() => {
          setConfirmOpen(false);
          setConfirmData(null);
        }}
        onOk={() => {
          if (!confirmData) return;
          dispatch(
            actions.refund({
              card_number: confirmData.tekmedi_card_number,
              transaction_Info_Id: confirmData.id,
              device_Id: '2044fbf6-545e-4303-9009-67afe3f7b1aa',
              employee_id: user?.id,
            }),
          );
        }}
      />
    </div>
  );
}
